import { createRouter, createMemoryHistory } from 'vue-router'
import ScreenOne from '../views/HomeView.vue'

const routes = [
  {
    path: '/',
    name: 'screen-one',
    component: ScreenOne
  },
  {
    path: '/screen-two',
    name: 'screen-two',
    component: () => import('../views/SecondView.vue')
  },
  {
    path: '/screen-three',
    name: 'screen-three',
    component: () => import('../views/SecondAfterView.vue')
  },
  {
    path: '/screen-four',
    name: 'screen-four',
    component: () => import('../views/ThirdView.vue')
  },
  {
    path: '/congratulations',
    name: 'congratulations',
    component: () => import('../views/CongratulationsPage.vue')
  }
]

const router = createRouter({
  history: createMemoryHistory(),
  routes
})

export default router
